.logo {
    display: flex;

    gap: 10px;

    align-items: center;

    height: 28px;

    &::before, &::after {
        display: inline-block;

        height: 15px;
        width: 31px;

        background: url("../../img/logo/logo_wrapper.svg") center / cover no-repeat;

        content: "";
    }
}
